import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { RoutingModule } from './app.routes';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';


import { AppComponent } from './app.component';
import { ProfileComponent } from './components/profile/profile.component';
import { HomeComponent } from './components/home/home.component';
import { SearchComponent } from './components/search/search.component';
import { SearchBarComponent } from './components/search/search-bar/search-bar.component';
import { SearchResultComponent } from './components/search/search-result/search-result.component';
import { NavbarComponent } from './components/profile/navbar/navbar.component';
import { AboutComponent } from './components/profile/about/about.component';
import { EducationComponent } from './components/profile/education/education.component';
import { ExperienceComponent } from './components/profile/experience/experience.component';
import { AchievementsComponent } from './components/profile/achievements/achievements.component';
import { ProjectsComponent } from './components/profile/projects/projects.component';
import { SkillsComponent } from './components/profile/skills/skills.component';
import { LoginComponent } from './components/home/login/login.component';
import { RegisterComponent } from './components/home/register/register.component';
import { HeroComponent } from './components/home/hero/hero.component';
import { FooterComponent } from './components/home/footer/footer.component';
import { ProjectsPopupComponent } from './components/profile/projects/projects-popup/projects-popup.component';
import { SkillsPopupComponent } from './components/profile/skills/skills-popup/skills-popup.component';
import { AboutPopupComponent } from './components/profile/about/about-popup/about-popup.component';
import { AchievementsPopupComponent } from './components/profile/achievements/achievements-popup/achievements-popup.component';
import { ExperiencePopupComponent } from './components/profile/experience/experience-popup/experience-popup.component';
import { EducationPopupComponent } from './components/profile/education/education-popup/education-popup.component';

// import { HttpClientModule } from '@angular/common/http'
import { AuthService } from './services/auth.service';
import { ValidateService } from './services/validate.service';



@NgModule({
  declarations: [
    AppComponent,
    ProfileComponent,
    HomeComponent,
    SearchComponent,
    SearchBarComponent,
    SearchResultComponent,
    NavbarComponent,
    AboutComponent,
    EducationComponent,
    ExperienceComponent,
    AchievementsComponent,
    ProjectsComponent,
    SkillsComponent,
    LoginComponent,
    RegisterComponent,
    HeroComponent,
    FooterComponent,
    ProjectsPopupComponent,
    SkillsPopupComponent,
    AboutPopupComponent,
    AchievementsPopupComponent,
    ExperiencePopupComponent,
    EducationPopupComponent,
   ],
  imports: [
    BrowserModule,
    RoutingModule,
    FormsModule,
    // HttpClientModule
    HttpModule
  ],

  providers: [AuthService, ValidateService],
  bootstrap: [AppComponent]
})
export class AppModule { }



